import React from "react"
import { graphql } from 'gatsby'
import BackButton from '../components/back-button'
import '../components/styles/custom.sass'
import '../components/styles/plyr.css'

class MusicTemplate extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      posts: props.data.markdownRemark
    }
  }

  render () {
    return (
      <div id="music">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="music-post" style={{ paddingTop: '25px' }}>
                <BackButton />
                <h1 className="template-h1">
                  {this.state.posts.frontmatter.title}
                </h1>
                <hr style={{ marginTop: '50px' }} />
                <div className="music-post-content" dangerouslySetInnerHTML={{ __html: this.state.posts.html }} />
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

}

export const postQuery = graphql`
  query musicPostByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date
        path
        title
        author
        template
        desc
      }
    }
  }
`;

export default MusicTemplate;
